.cars__filter-search {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 32px;
    flex-wrap: wrap;
    padding: 16px 0px;
    border-bottom: 2px solid rgb(220, 220, 220, 0.5);
}

.cars-filter-search-item {
    display: flex;
    align-items: center;
    gap: 8px;
}

.cars__search-dependencies-wrapper {
    position: relative;
    flex-grow: 1;
}

.cars__search-filter {
    box-sizing: border-box;
    width: 100%;
    border: none;
    outline: none;
    padding: 8px;
    background-color: rgba(220, 220, 220, 0.5);
    border-radius: 8px;
}

.cars__search-filter-error {
    font-size: 10px;
    left: 8px;
    bottom: -12px;
    position: absolute;
    color: red;
}

.cars__search-name-dependecies {
    display: block;
    flex-basis: 48px;
    flex-shrink: 0;
    /* margin-right: 8px; */
}

.car__filter-action-button-container {
    display: flex;
    justify-content: space-between;
    gap: 32px;
}

.car__filter-button {
    border: none;
    padding: 8px;
    border-radius: 8px;
    transition: transform linear 100ms;
}

.car__filter-button:hover {
    transform: scale(1.1);
}

@media (max-width: 786px) {
    .cars__filter-search {
        flex-direction: column;
        align-items: stretch;
        padding-right: 32px;
        padding-left: 32px;

    }
    .car__filter-action-button-container {
        align-self: stretch;
        display: flex;
        justify-content: space-around;
        gap: 32px;
    }
}